body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;

  color: #303030;
  font-family: Muli,-apple-system,BlinkMacSystemFont,'Segoe UI',Roboto,'Helvetica Neue',Arial,sans-serif,'Apple Color Emoji','Segoe UI Emoji','Segoe UI Symbol','Noto Color Emoji';
  min-height: 100vh;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav-center{
    background-color: #958694;
    color: white;
}

.nav-link{
    color:white;
    font-weight: 500;
    font-size: large;
}

.profile-img {
    max-height: 100%;
    max-width: 100%;
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
}

.section:before {
    display: block; 
    content: " "; 
    margin-top: -75px; 
    height: 75px; 
    visibility: hidden; 
}

.profile-img-container {
    justify-content: center;
    display: block;
}

.resume-comp {
    margin: 20px;
    padding: 10px;
    text-align: justify;
}

.publication:hover {
    background-color: #F1E6E4;
    cursor: default;
}

.padding {
    padding: 10px;
}

Code {
    color: inherit;
    width: 100%;
}

.linkdiv, .nameemaildiv{
    text-align: center;
}

.linkdiv{
    padding-top: 10px;
}

.aboutmediv{
    padding-bottom: 10px;
}

.social {
    padding: 5px;
    margin: 1px;
    margin-right: 5px;
    background-color: #BF838D;
    border-radius: 100%;
    color: white;
    text-align: center;
    width: 35px;
    height: 35px;
    display: inline-block;
}

.social:hover {
    color: #F1E6E4;
}

.center-content {
    text-align: center;
}

hr {
    margin-bottom: 0px;
}

a, a:hover, b {
    color: #BF838D
}

.badge-secondary{
    background-color: #958694;
}
